<template>
  <div class="forgetpassword">
    <van-nav-bar v-model="active" title="忘记密码" left-text="返回" left-arrow @click-left="back" />
    <div class="form-box">
      <div v-if="isEmail">
        <van-form>
          <van-field v-model="emailInfo.email" center clearable label="邮箱" placeholder="请输入邮箱">
            <template #button v-if="isEmail">
              <van-button size="small" type="primary" :disabled="isSend" @click="sendCode">{{text}}</van-button>
            </template>
          </van-field>
          <van-field
            v-model="emailInfo.validCode"
            type="text"
            label="邮箱验证码"
            placeholder="请填写6位邮箱验证码"
          />
          <div style="margin: 16px;">
            <van-button round block type="info" native-type="submit" @click="suerVerification">下一步</van-button>
          </div>
        </van-form>
      </div>
      <div v-else>
        <van-form>
          <van-field label="手机号" placeholder="请输入11位手机号" v-model="newInfo.phone" />
          <van-field
            :type="isPassword ? 'password':'text'"
            label="新密码"
            placeholder="请输入6-16位且为字母开头的新密码"
            :right-icon="isPassword ? 'closed-eye':'eye-o'"
            v-model="newInfo.password"
            @click-right-icon="showPassword"
          />
          <div style="margin: 16px;">
            <van-button round block type="info" native-type="submit" @click="suerNewPassword">确认</van-button>
          </div>
        </van-form>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/forgetpassword.less";
import { utils } from "../assets/js/utils";
import { validForm } from "../assets/js/validForm";

export default {
  data() {
    return {
      active: 0,
      isSend: false,
      isPassword: true,
      text: "发送验证码",
      emailInfo: {
        email: "",
        validCode: "",
      },
      newInfo: {
        phone: "",
        password: "",
      },
      isEmail: true,
    };
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    showPassword() {
      this.isPassword = !this.isPassword;
    },
    //发送验证码
    sendCode() {
      let time = 5;
      this.isSend = true;
      this.text = time + "s后重新发送";

      let timer = setInterval(() => {
        if (time > 0) {
          time--;
          this.text = time + "s后重新发送";
        } else {
          clearInterval(timer);
          this.text = "发送验证码";
          this.isSend = false;
        }
      }, 1000);

      //发起获取邮箱验证码请求
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      let data = {
        appkey: this.appkey,
        email: this.emailInfo.email,
      };
      data = utils.queryString(data);
      this.axios({
        method: "POST",
        url: "/emailValidCode",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          this.$toast(result.data.msg);
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
    //验证邮箱验证码
    suerVerification() {
      let verificationInfo = {
        email: {
          value: this.emailInfo.email,
          reg: /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,
          errorMsg: "邮箱格式不正确",
        },
        validCode: {
          value: this.emailInfo.validCode,
          reg: /^\d{6}$/,
          errorMsg: "验证码为6位数字",
        },
      };
      //验证
      if (!validForm.valid(verificationInfo)) {
        return;
      }
      //发起验证邮箱验证码请求
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      let data = {
        appkey: this.appkey,
        validCode: this.emailInfo.validCode,
      };
      data = utils.queryString(data);
      this.axios({
        method: "POST",
        url: "/checkValidCode",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          this.$toast(result.data.msg);
          if (result.data.code == "K001") {
            this.isEmail = false;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
    //修改密码
    suerNewPassword() {
      let modifyInfo = {
        phone: {
          value: this.newInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: "手机号格式不正确",
        },
        password: {
          value: this.newInfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "密码为字母开头6-16位",
        },
      };
      //验证
      if (!validForm.valid(modifyInfo)) {
        return;
      }
      //发起找回密码请求
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      let data = {
        appkey: this.appkey,
        phone: this.newInfo.phone,
        password: this.newInfo.password,
      };
      data = utils.queryString(data);
      this.axios({
        method: "POST",
        url: "/retrievePassword",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          this.$toast(result.data.msg);
          if (result.data.code == "L001") {
            setTimeout(() => {
              this.$router.push({ name: "Login" });
            }, 500);
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
  },
};
</script>

<style lang="less" scoped>
</style>